// Use this file to overwrite the basic Bootstrap variables and add your own variables
// To overwrite a Bootstrap variable you dont have to touch the Bootstrap folder. 
// Just copy a variable from /sass/bootstrap/_variables.scss, paste it here and edit the value.
// As example you find one of the most important Bootstrap variable below: $brand-primary.
// Thats the ugly Bootstrap blue used for all the links, primary buttons etc.
// Just add your color code here, compile the theme.scss file to change this color everywhere


//
// Color system
//

$primary: #000;
$hennepens-green: #356e3e;


//
// Custom Fonts
//

@font-face {
    font-family: 'libre_baskervillebold';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/librebaskerville-bold-webfont.woff2') format('woff2'),
         url('../fonts/librebaskerville-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'libre_baskervilleitalic';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/librebaskerville-italic-webfont.woff2') format('woff2'),
         url('../fonts/librebaskerville-italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'libre_baskervilleregular';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/librebaskerville-regular-webfont.woff2') format('woff2'),
         url('../fonts/librebaskerville-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


@font-face {
    font-family: 'superiorregular';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/superior-regular-webfont.woff2') format('woff2'),
         url('../fonts/superior-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'proxima_novaregular';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/proximanova-regular-webfont.woff2') format('woff2'),
         url('../fonts/proximanova-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'proxima_novabold';
    src: local('Open Sans'), local('Open Sans'), url('../fonts/proxima_nova_bold-webfont.woff2') format('woff2'),
         url('../fonts/proxima_nova_bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face{
  font-family: 'linearicons';
  src:  url('../fonts/linearicons.eot?mlscn1#iefix') format('embedded-opentype'), url('../fonts/linearicons.ttf') format('truetype'), url('../fonts/linearicons.woff') format('woff'), url('../fonts/linearicons.svg?#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?1d56ub');
  src:  url('../fonts/icomoon.eot?1d56ub#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?1d56ub') format('truetype'),
    url('../fonts/icomoon.woff?1d56ub') format('woff'),
    url('../fonts/icomoon.svg?1d56ub#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}


$font-family-icons: 'icomoon';
$font-family-libre-baskerville-regular: 'libre_baskervilleregular';
$font-family-libre-baskerville-italic: 'libre_baskervilleitalic';
$font-family-libre-baskerville-bold: 'libre_baskervillebold';
$font-family-superior-regular: 'superiorregular';
$font-family-sans-serif: 'proxima_novaregular';
$font-family-sans-serif-bold: 'proxima_novabold';
